import React,{ useState, useContext, useEffect } from 'react';
import firebase from 'firebase';
import PropTypes from 'prop-types'
import { AuthContext } from 'contexts/AuthContext';
const initalState = {
    id:0,
    user:'Unknown',
    content:'',
    like:0,
    createdAt:new Date()
}

const FormAddComment = (props) => {
    const { onSubmitComment } = props
    const [ comment, setComment ] = useState(initalState)
    const { userInfo } = useContext(AuthContext)
    const [isSignedIn, setIsSignedIn] = useState(false); // Local signed-in state.

    // Listen to the Firebase Auth state and set the local state.
    useEffect(() => {
        const unregisterAuthObserver = firebase.auth().onAuthStateChanged(user => {
            setIsSignedIn(!!user);
        });
        return () => unregisterAuthObserver(); // Make sure we un-register Firebase observers when the component unmounts.
    }, []);
    const handleChangeValue = (e) => {
        const { name, value } = e.target
        const temp = {
            ...comment,
            [name]: value
        }
        setComment(temp)
    }
    const handleSubmitForm = (e) => {
        e.preventDefault()
        if(!onSubmitComment) return
        const newComment = {
            ...comment,
            user: isSignedIn ? firebase.auth().currentUser.displayName : 'Unknown',
            id: Math.ceil(Math.random() * 1000000000),
            createdAt:new Date()
        }
        onSubmitComment(newComment)
        setComment(initalState)
    }
    return (
        <form onSubmit={handleSubmitForm}>
            <input 
                type="text" 
                name="content" 
                value={comment.content} 
                onChange={handleChangeValue} 
                placeholder="Nhập nội dung..." 
                style={{height: '40px', paddingLeft: '12px',borderRadius:999,outline:'none'}}
            />
        </form>
    );
}
FormAddComment.propTypes = {
    onSubmitComment: PropTypes.func
};
export default FormAddComment;