/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable jsx-a11y/alt-text */
/* eslint-disable array-callback-return */
import Aside from 'components/Aside';
import Breadcrumb from 'components/Breadcrumb';
import { useEffect, useState } from 'react';

const NewsDetail = ({match}) => {
    const [ news, setNews ] = useState({})
    const listBreadcrumb = [
        {
          title: 'Trang chủ',
          link:'',
          isActive: false
        },
        {
          title: 'Tin tức',
          link:'news',
          isActive: true
        },
        {
          title: news.title,
          link:'#',
          isActive: true
        },
      ]
      let url = "https://newsapi.org/v2/everything?q=tesla&from=2021-07-12&sortBy=publishedAt&apiKey=0606617d9d5a4f11823f1eacea146e9c"
      useEffect(()=>{
        const getNews = async () => {
            try {
                const res = await fetch(url)
                const datas = await res.json()
                const data = datas.articles.find(news=>news.publishedAt === match.params.id)
                console.log(datas.articles)
                setNews(data)
            } catch (error) {
                console.log(error)
            }
        }
        getNews()
    },[match.params.id])
      
    return ( 
        <>
        <div className="container">
            <div className="row">
                <div className="col-md-9 pt-3">
                <Breadcrumb listBreadcrumb={listBreadcrumb} />

                    <div className="info-wrapper">
                        <div className="info-title">
                            <h2 className="title text-md-center">{news.title}</h2>
                        </div>
                        <div className="info-main">
                            <div className="info-main__left">
                                <img src={news.urlToImage} height="400px" />
                            </div>
                            <div className="info-main__right">
    
                         
                
                                <div className="film-info">
                                    <p className="film-info__left">Lượt xem: </p>
                                    <p className="film-info__right">235 <i  className="far fa-eye"></i></p>
                                </div>
                                <div className="film-info">
                                    <p className="film-info__left">Tác giả: </p>
                                    <p className="film-info__right">{news.author}</p>
                                </div>
                                <div className="film-info">
                                    <p className="film-info__left">Thời gian: </p>
                                    <p className="film-info__right">{news.publishedAt}</p>
                                </div>
                                <div className="film-description">
                                    <h6 className="film-description__title">Nội dung</h6>
                                    <p className="film-description__content">{news.content}</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="col-md-3 pt-3">
                    <Aside />
                </div>
            </div>
        </div>
        </>
     );
}
NewsDetail.propTypes = {
    
}
export default NewsDetail;