import firebase from 'firebase';
import React, { useEffect, useState } from 'react';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
// import { Redirect } from 'react-router-dom';

// Configure Firebase.
const config = {
  apiKey: 'AIzaSyCv2HnEB78CQh_2ee_wdA_zsGvTtNMhImM',
  authDomain: 'genzfilm-a4239.firebaseapp.com',
};
firebase.initializeApp(config);

// Configure FirebaseUI.
const uiConfig = {
  // Popup signin flow rather than redirect flow.
  signInFlow: 'popup',
  // We will display Google and Facebook as auth providers.
  signInOptions: [
    firebase.auth.GoogleAuthProvider.PROVIDER_ID,
    firebase.auth.FacebookAuthProvider.PROVIDER_ID
  ],
  callbacks: {
    // Avoid redirects after sign-in.
    signInSuccessWithAuthResult: () => false,
  },
};

Login.propTypes = {
    
};
function Login() {
    const [isSignedIn, setIsSignedIn] = useState(false); // Local signed-in state.

    // Listen to the Firebase Auth state and set the local state.
    useEffect(() => {
        const unregisterAuthObserver = firebase.auth().onAuthStateChanged(user => {
            setIsSignedIn(!!user);
        });
        return () => unregisterAuthObserver(); // Make sure we un-register Firebase observers when the component unmounts.
    }, []);

    if (!isSignedIn) {
        return (
          <div className="container pt-3 m-auto text-md-center">
            <h1>GenZ Film</h1>
            <p>Please sign-in:</p>
            <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={firebase.auth()} />
          </div>
        );
      }
    return (
        <div className="container pt-3">
            <h1 className="title text-md-center">Xin chào {firebase.auth().currentUser.displayName}!</h1>
            <button type="button" className="btn btn-danger" onClick={() => firebase.auth().signOut()}>Đăng xuất</button>
        </div>
       
    );
}

export default Login;